.projectsBlock {
    min-height: 100vh;
    background-color: #d8f66b;
}

.title {
    background-color: darkkhaki;
}

.projectsContainer {
    align-items: center;
    flex-direction: column;
    justify-content: flex-start;
}

.projects{
    border: 1px solid;
    width: 100%;
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
}